import React, { useState, useEffect } from 'react';
import { NavLink, useParams, useNavigate } from 'react-router-dom';
import CreateIcon from '@mui/icons-material/Create';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import WorkIcon from '@mui/icons-material/Work';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';

const Details = () => {
  const { id } = useParams();
  const [userData, setUserData] = useState(null); // State to store user data
  const [loading, setLoading] = useState(true); // State to handle loading
  const [error, setError] = useState(null); // State to handle errors
  const navigate = useNavigate();
  const [deleteStatus, setDeleteStatus] = useState(null);


  const deleteuser = async (id) => {
    setDeleteStatus(null);
    try {
        const res2 = await fetch(`/deleteuser/${id}`, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json"
            }
        });

        if (!res2.ok) {
            throw new Error(`Error: ${res2.statusText}`);
        }

        const deletedata = await res2.json();
      

        if (!deletedata) {
           
            setDeleteStatus("Error: Failed to delete user.");
        } else {
            
            setDeleteStatus("User successfully deleted.");
            navigate("/");
          
        }
    } catch (error) {
        console.error("Error during deletion:", error);
        setDeleteStatus(`Error: ${error.message}`);
    }
}

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch(`/getuser/${id}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json"
          }
        });

        if (!res.ok) {
          throw new Error(`Error: ${res.statusText}`);
        }

        const data = await res.json();
        setUserData(data); // Set fetched data to userData state
      } catch (error) {
        setError(error.message); // Set error message to error state
      } finally {
        setLoading(false); // Stop loading once the fetch is done
      }
    };

    fetchData();
  }, [id]);

  if (loading) {
    return <div>Loading...</div>; // Show loading message while data is being fetched
  }

  if (error) {
    return <div>Error: {error}</div>; // Show error message if there's an error
  }

  if (!userData) {
    return <div>No user data found</div>; // Show message if no user data is found
  }

  return (
    <div className='container mt-3'>
      <h1 style={{ fontWeight: 400 }}>Welcome {userData.name}</h1>
      <Card sx={{ maxWidth: 600 }}>
        <CardContent>
          <div className="add_btn">
            <NavLink to= {`/edit/${id}`}><button className="btn btn-primary mx-2"><CreateIcon /></button></NavLink>  
            <button onClick={()=>deleteuser(id)} className="btn btn-danger"><DeleteOutlineIcon /></button>
          </div>
          <div>
          <img src="/profile.png" style={{ width: 60, borderRadius: '50%' }} alt="Profile" />
          </div>
          <div className="row">
            <div className='left_view col-lg-6 col-md-6 col-12'>
              <h3 className='mt-3'>Name: <span>{userData.name}</span></h3>
              <h3 className='mt-3'>Age: <span>{userData.age}</span></h3>
              <p><MailOutlineIcon /> Email: <span>{userData.email}</span></p>
              <p><WorkIcon /> Occupation: <span>{userData.work}</span></p>
            </div>
            <div className='right_view col-lg-6 col-md-6 col-12'>
              <p className='mt-3'><PhoneAndroidIcon /> Mobile: <span>{userData.mobile}</span></p>
              <p className='mt-3'><LocationOnIcon /> Location: <span>{userData.add}</span></p>
              <p className='mt-3'>Description: <span>{userData.desc}</span></p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}

export default Details;
