import React, { useEffect, useState, useContext } from "react";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import CreateIcon from "@mui/icons-material/Create";
import DeleteOutlineIcon from "@mui/icons-material/DeleteOutline";
import { NavLink } from "react-router-dom";
import { adddata, updata } from "./context/ContextProvider";

const Home = () => {
  const [getuserdata, setUserdata] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [deleteStatus, setDeleteStatus] = useState(null);

  const { udata, setUdata } = useContext(adddata);
  const { edata, setEdata } = useContext(updata); // Context for edit success

  // Fetch data from the server
  const getdata = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch("/getdata", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (!res.ok) {
        throw new Error(`Error: ${res.statusText}`);
      }

      const data = await res.json();
      setUserdata(data);
      setLoading(false);
    } catch (error) {
      console.error("Error during fetch:", error);
      setError(error.message);
      setLoading(false);
    }
  };

  useEffect(() => {
    getdata();
  }, [edata]); // Refresh data when edit success data changes

  const deleteuser = async (id) => {
    setDeleteStatus(null);
    try {
      const res2 = await fetch(`/deleteuser/${id}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (!res2.ok) {
        throw new Error(`Error: ${res2.statusText}`);
      }

      const deletedata = await res2.json();
      

      if (!deletedata) {
        
        setDeleteStatus("Error: Failed to delete user.");
      } else {
        
        setDeleteStatus("User successfully deleted.");
        getdata(); // Refresh the list
      }
    } catch (error) {
      console.error("Error during deletion:", error);
      setDeleteStatus(`Error: ${error.message}`);
    }
  };

  // Conditional rendering based on state
  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error}</div>;
  }

  return (
    <>
      {udata ? (
        <>
          <div
            className="alert alert-success alert-dismissible fade show"
            role="alert"
          >
            <strong>Success!</strong> User added successfully!
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
              onClick={() => setUdata("")} // Clear success message on close
            ></button>
          </div>
        </>
      ) : (
        ""
      )}

      {edata ? (
        <>
          <div
            className="alert alert-success alert-dismissible fade show"
            role="alert"
          >
            <strong>Success!</strong> User edited successfully!
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
              onClick={() => setEdata("")} // Clear success message on close
            ></button>
          </div>
        </>
      ) : (
        ""
      )}

      <div className="mt-5">
        <div className="container">
          <div className="add_btn mt-2 mb-2">
            <NavLink to="/register" className="btn btn-primary">
              Add Data
            </NavLink>
          </div>
          {deleteStatus && (
            <div
              className={`alert ${
                deleteStatus.includes("Error")
                  ? "alert-danger"
                  : "alert-warning"
              }`}
            >
              {deleteStatus}
            </div>
          )}
          {getuserdata.length === 0 ? (
            <div>No data available.</div>
          ) : (
            <table className="table">
              <thead>
                <tr className="table-dark">
                  <th scope="col">ID</th>
                  <th scope="col">UserName</th>
                  <th scope="col">Email</th>
                  <th scope="col">Job</th>
                  <th scope="col">Number</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <tbody>
                {getuserdata.map((user, index) => (
                  <tr key={user._id}>
                    <th scope="row">{index + 1}</th>
                    <td>{user.name}</td>
                    <td>{user.email}</td>
                    <td>{user.work}</td>
                    <td>{user.mobile}</td>
                    <td className="d-flex justify-content-between">
                      <NavLink to={`view/${user._id}`}>
                        <button className="btn btn-success">
                          <RemoveRedEyeIcon />
                        </button>
                      </NavLink>
                      <NavLink to={`edit/${user._id}`}>
                        {" "}
                        <button className="btn btn-primary">
                          <CreateIcon />
                        </button>
                      </NavLink>
                      <button
                        onClick={() => deleteuser(user._id)}
                        className="btn btn-danger"
                      >
                        <DeleteOutlineIcon />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </>
  );
};

export default Home;
