import React, { useContext, useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { adddata } from './context/ContextProvider';


const Register = () => {

  const {udata,setUdata} = useContext(adddata);
  const [inpval, setINP] = useState({
    name: "",
    email: "",
    age: "",
    mobile: "",
    work: "",
    add: "",
    desc: ""
  });

  const navigate = useNavigate();

  const setdata = (e) => {
    const { name, value } = e.target;
    setINP((preval) => ({
      ...preval,
      [name]: value
    }));
  };

  const addinpdata = async (e) => {
    e.preventDefault();

    const { name, email, age, mobile, work, add, desc } = inpval;

    try {
      const res = await fetch("/register", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          name, email, age, mobile, work, add, desc
        })
      });

      if (!res.ok) {
        const errorData = await res.json();
        alert(`Error: ${errorData.error || 'Unknown error'}`);
       
      } else {
        const data = await res.json();
        setUdata(data);
        
        navigate('/');
      }
    } catch (error) {
      console.error("Error during fetch:", error);
      alert("An error occurred while adding data");
    }
  }

  return (
    <div className="container">
      <form className="mt-4" onSubmit={addinpdata}>
        <div className="row">
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="name" className="form-label">Name</label>
            <input type="text" name="name" value={inpval.name} onChange={setdata} className="form-control" id="name" />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="email" className="form-label">Email</label>
            <input type="email" name="email" value={inpval.email} onChange={setdata} className="form-control" id="email" />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="age" className="form-label">Age</label>
            <input type="number" name="age" value={inpval.age} onChange={setdata} className="form-control" id="age" />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="mobile" className="form-label">Mobile</label>
            <input type="tel" name="mobile" value={inpval.mobile} onChange={setdata} className="form-control" id="mobile" />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="work" className="form-label">Work</label>
            <input type="text" name="work" value={inpval.work} onChange={setdata} className="form-control" id="work" />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label htmlFor="add" className="form-label">Address</label>
            <input type="text" name="add" value={inpval.add} onChange={setdata} className="form-control" id="add" />
          </div>
          <div className="mb-3 col-lg-12 col-md-12 col-12">
            <label htmlFor="desc" className="form-label">Description</label>
            <textarea name="desc" value={inpval.desc} onChange={setdata} className="form-control" id="desc" cols="30" rows="5"></textarea>
          </div>
          <button type="submit" className="btn btn-primary">Submit</button>
        </div>
      </form>
    </div>
  );
};

export default Register;

